import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { loginUser } from '../api/googleScriptApi';
import { useLoader } from '../context/LoaderProvider'; // <-- Fix this import
import PageLoader from './PageLoader'; // <-- Use this import

const LoginPage = () => {
  const [role, setRole] = useState('user');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const { loading, setLoading } = useLoader(); // <-- Use global loader
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true); // Start global loader
    try {
      const result = await loginUser({ username, password, role });
      if (result.success) {
        localStorage.setItem('auth', JSON.stringify({ role, username, token: result.token }));
        if (role === 'admin') navigate('/admin');
        else navigate('/');
      } else {
        setError('Invalid credentials');
      }
    } catch {
      setError('Login failed. Try again.');
    } finally {
      setLoading(false); // Stop global loader
    }
  };

  return (
    <div className="container-fluid mt-5 px-2 px-md-4">
      {loading && <PageLoader />}
      <div className="row justify-content-center">
        <div className="col-12 col-sm-8 col-md-6 col-lg-5">
          <div className="card shadow" style={{ background: 'linear-gradient(135deg, #f8fafc 60%, #cfe2ff 100%)' }}>
            <div className="card-header bg-primary text-white">
              <h2 className="mb-0 text-center">Login</h2>
            </div>
            <div className="card-body">
              {error && <div className="alert alert-danger">{error}</div>}
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label className="form-label fw-bold">Role</label>
                  <select className="form-select" value={role} onChange={e => setRole(e.target.value)} disabled={loading}>
                    <option value="user">Employee</option>
                    <option value="admin">Admin</option>
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label fw-bold">Username</label>
                  <input
                    type="text"
                    className="form-control"
                    value={username}
                    onChange={e => setUsername(e.target.value)}
                    required
                    disabled={loading}
                  />
                </div>
                <div className="mb-4">
                  <label className="form-label fw-bold">Password</label>
                  <input
                    type="password"
                    className="form-control"
                    value={password}
                    onChange={e => setPassword(e.target.value)}
                    required
                    disabled={loading}
                  />
                </div>
                <button type="submit" className="btn btn-primary w-100 fw-bold" disabled={loading}>
                  {loading ? 'Logging in...' : 'Login'}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;