import axios from 'axios';

const BASE_URL = 'https://script.google.com/macros/s/AKfycbyhX8mCXu0DbuQu4xRKuccFEVpZupmeRVeT2ooY3s9abXY_XOltgg64chWOHFxsgyE8/exec';

// GET - fetch leave requests
export const fetchLeaveRequests = async () => {
  const response = await axios.get(`${BASE_URL}?action=getLeaveRequests`);
  return response.data;
};

// GET - submit leave request
export const submitLeaveRequest = async ({ name,type,  startDate, endDate, reason }) => {
  const params = new URLSearchParams({
    action: 'submitLeaveRequest',
    name,
    type,
    startDate,
    endDate,
    reason,
  });
  const response = await axios.get(`${BASE_URL}?${params}`);
  return response.data;
};

// GET - login user
export const loginUser = async ({ username, password, role }) => {
  const params = new URLSearchParams({ action: 'login', username: username, password: password, role: role });
  const response = await axios.get(`${BASE_URL}?${params}`);
  return response.data;
};

// POST - update leave status
export const updateLeaveStatus = async (leave, status) => {
    console.log({...leave}, 'leave')
  const params = new URLSearchParams({ action: 'updateLeaveStatus', ...leave, status });
  console.log(`${params}`, 'params')
  const response = await axios.get(`${BASE_URL}?${params}`);
  return response.data;
};

export const fetchUserLeaveRequests = async (username) => {
  const response = await axios.get(`${BASE_URL}?action=getUserLeaveRequests&username=${encodeURIComponent(username)}`);
  return response.data;
};

// Send a user query/feedback (GET)
export async function sendUserQuery(username, message) {
    const params = new URLSearchParams({
        action: 'saveUserQuery',
        username,
        message
    });
    const response = await axios.get(`${BASE_URL}?${params}`);
    return response.data;
}

